import React, { useCallback, useEffect, useMemo, useState } from 'react'
import { Grid, Divider } from '@mui/material'
import { styled } from '@mui/material/styles'
import { BTitle, Title } from 'components/CommonComponent'
import { useWeb3React } from 'web3-react-core'
import Tabs from 'components/Tabs'
import Card from 'components/Card'
import { Button, useWalletModal } from 'components/uikit'
import contracts from 'config/constants/contracts'
import { formatNumberWithUnits } from 'utils/formatTheValue'
import { getContract, isSupportedChainId } from 'utils/web3React'
import useToast from 'hooks/useToast'
import useHashPowerToken from 'hooks/useHashPowerToken'
import MinerABI from 'config/abi/Miner.json'
import { TokenUtils, toLargeUnits, toSmallUnits } from 'utils/transformHelper'
import useAuth from 'hooks/useAuth'
import BlockUpdaterABI from 'config/abi/BlockUpdater.json'
import { SupportedChainId, valueToKeyMap } from 'config/constants/tokenConfig'
import { blastSepoliaMintTokens, mintTokenChainConfigs, MintTokenKey } from 'config/constants/mintToken'
import { BigNumber, ethers } from 'ethers'
import { usePositionInfo } from 'hooks/news/usePoistion'
import Multicall from 'utils/multicall'
import { TransactionSubmittedInfo } from 'components/TransactionInfo'
import { ToastTitle } from 'config/constants/contants'
import HoldingsWallet from './components/HoldingsWallet'
import OutstandingDebt from './components/OutstandingDebt'
import useSystemParams from '../../hooks/useSystemParams'
import useEpoch from '../../hooks/useEpoch'
import { RewardData } from './type'

const Wrapper = styled('div')`
  width: 100%;
  height: 100%;
  overflow: scroll;
`
const Box = styled('div')`
  box-sizing: border-box;
  margin-top: 26px;
  padding-right: 44px;
  min-width: 1024px;
  padding-bottom: 100px;

  &::-webkit-scrollbar {
    display: none;
  }
`

const CommonCard = styled(Card)`
  padding: 30px;
  height: 100%;

  &.rightPanel {
    h4 {
      line-height: 20px;
    }
  }
  h4 {
    font-family: PingFangSC-Medium, sans-serif;
    font-weight: 480;
    font-size: 16px;
    line-height: 22px;
    color: black;
  }

  p {
    font-family: PingFangSC-Semibold, sans-serif;
    font-size: 40px;
    font-weight: 550;
    line-height: 1;
    color: #6ed6f3;
    margin-top: 12px;
    margin-bottom: 8px;
    letter-spacing: 1px;
    sub {
      font-size: 16px;
    }
  }
  span {
    font-family: PingFangSC-Regular, sans-serif;
    font-size: 14px;
  }
`
const SubTitle = styled(Title)`
  margin-bottom: 14px;
  font-family: PingFangSC-Semibold, sans-serif;
  font-weight: 550;
  font-size: 22px;
  line-height: 31px;
  color: #000000;
`

const ClaimButton = styled(Button)`
  background: linear-gradient(90deg, #61c2f7 0%, #7ae9f0 100%) !important;
  border-radius: 20px !important;
  box-shadow: none !important;
  height: 30px !important;
  line-height: 30px !important;
  color: #000 !important;
  font-size: 14px !important;
  display: block !important;

  &.disabled {
    background: #ddd !important;
    color: #eee !important;
  }
`

// const TableCard = styled(Card)`
//   margin-bottom: 20px;
// `

const getEstimatedDailyReward = (lastEpochReward: BigNumber, balance: BigNumber) => {
  return Math.floor((Number(lastEpochReward) * Number(balance)) / 1e18)
}
const { claimHelper, uniswapV2Multicall } = contracts
const minerInterface = new ethers.utils.Interface(MinerABI)
const blockUpdaterInterface = new ethers.utils.Interface(BlockUpdaterABI)

function MyPage() {
  const { prevEpoch } = useEpoch()
  const [claimStatus, setClaimStatus] = useState(false)
  const { walletTokenBalance } = useHashPowerToken()
  const { signer } = useSystemParams()
  const { chainId, account, active } = useWeb3React()
  const { toastError, toastSuccess, toastInfo } = useToast()
  const [walletTotal, setWalletTotal] = useState(0)
  const [avatarTotal, setAvatarTotal] = useState(0)
  const [dailyInterestRewards, setDailyInterestRewards] = useState(0)
  const [dailyObligation, setDailyObligation] = useState(0)
  const [rewardsData, setRewardsData] = useState<RewardData>(
    Object.values(blastSepoliaMintTokens).reduce((acc, curr) => {
      return {
        ...acc,
        [curr.symbol]: {
          walletRewards: {
            epochReward: ethers.constants.Zero,
          },
          avatarRewards: {
            epochReward: ethers.constants.Zero,
          },
          walletEstimateDailyReward: 0,
          avatarEstimateDailyReward: 0,
        },
      }
    }, {}) as RewardData,
  )
  const [allRewards, setAllRewards] = useState([])
  const { login, logout } = useAuth()
  const { onPresentConnectModal } = useWalletModal(login, logout, account)

  const isLogin = useMemo(() => {
    return active && chainId && isSupportedChainId(chainId)
  }, [active, chainId])
  const { positionAll, tokenPrice, allowedTokens } = usePositionInfo()

  const claimAll = useCallback(async () => {
    if (!account) {
      onPresentConnectModal()
      return
    }

    if (signer) {
      const claimContract = getContract(signer, claimHelper.abi, claimHelper.address[chainId])
      if (claimContract) {
        try {
          const params = []
          for (const item of Object.values(positionAll)) {
            params.push([item.mintTokenInfo.address, account])
            if (item.isEnable) {
              params.push([item.mintTokenInfo.address, item.positionAddress])
            }
          }
    
          setClaimStatus(true)
          const txInfo = await claimContract.multipleClaim(params)
          if (txInfo) {
            toastInfo(
              ToastTitle.submited,
              <TransactionSubmittedInfo txHash={txInfo.hash} content="Waiting for confirmation" />,
            )
            await txInfo.wait()
            toastSuccess(
              ToastTitle.success,
              <TransactionSubmittedInfo txHash={txInfo.hash} content="Transaction has been confirmed" />,
            )
            setClaimStatus(false)
          }
        } catch (e: any) {
          setClaimStatus(false)
          toastError(ToastTitle.failed, e?.reason)
        }
      }
    }
  }, [account, signer, onPresentConnectModal, chainId, positionAll, toastInfo, toastSuccess, toastError])

  const getPendingReward = useCallback(async () => {
    if (isLogin && walletTokenBalance) {
      const _tokens = mintTokenChainConfigs[chainId as SupportedChainId].tokens
      const multicall = new Multicall(chainId, uniswapV2Multicall.address, uniswapV2Multicall.abi)

      for (const key in _tokens) {
        if (Object.prototype.hasOwnProperty.call(_tokens, key)) {
          const tokenInfo = _tokens[key as MintTokenKey]
          const positionInfo = positionAll[key as MintTokenKey]
          const walletBalance = walletTokenBalance[tokenInfo.symbol] || 0
          let collateralBalance = 0

          if (positionInfo && positionInfo.isEnable && allowedTokens[positionInfo.positionAddress]) {
            // eslint-disable-next-line @typescript-eslint/no-shadow
            const { balances, tokens } = allowedTokens[positionInfo.positionAddress]
            const tokenBalances = Object.keys(balances).reduce((acc, curr) => {
              return {
                ...acc,
                [curr]: toLargeUnits(balances[curr], tokens[curr].decimals, true) as number,
              }
            }, {})
            collateralBalance = tokenBalances[tokenInfo.symbol] || 0
          }

          const tokenRewards = {
            walletRewards: {
              epochReward: ethers.constants.Zero,
            },
            avatarRewards: {
              epochReward: ethers.constants.Zero,
            },
            walletEstimateDailyReward: 0,
            avatarEstimateDailyReward: 0,
          }
          let walletHasRweards = false
          let positionHasRweards = false

          const holderInfoMappingCalls = []
          if (walletBalance) {
            walletHasRweards = true
            holderInfoMappingCalls.push({
              target: tokenInfo.address,
              callData: minerInterface.encodeFunctionData('holderInfoMapping', [account]),
              functionSignature: 'holderInfoMapping',
            })
          }
          if (positionInfo.isEnable && collateralBalance > 0) {
            positionHasRweards = true
            holderInfoMappingCalls.push({
              target: tokenInfo.address,
              callData: minerInterface.encodeFunctionData('holderInfoMapping', [positionInfo.positionAddress]),
              functionSignature: 'holderInfoMapping',
            })
          }
          multicall.addAbi(tokenInfo.address, MinerABI)
          multicall.addAbi(tokenInfo.blockUpdater, BlockUpdaterABI)
          if (holderInfoMappingCalls.length === 0) {
            continue
          }
          const {
            returnData: { holderInfoMapping },
          } = await multicall.aggregate(holderInfoMappingCalls)
          const pendingRewardCalls = []
          if (holderInfoMappingCalls.length === 1) {
            if (walletHasRweards) {
              const holderInfos = holderInfoMapping[0]
              // holderInfoMapping
              pendingRewardCalls.push({
                target: tokenInfo.blockUpdater,
                callData: blockUpdaterInterface.encodeFunctionData('pendingReward', [
                  toSmallUnits(String(walletBalance), tokenInfo.decimals),
                  holderInfos[0],
                  holderInfos[1],
                  holderInfos[2],
                ]),
                functionSignature: 'pendingReward',
              })
            }
            if (positionHasRweards) {
              const holderInfos = holderInfoMapping[0]
              // holderInfoMapping
              pendingRewardCalls.push({
                target: tokenInfo.blockUpdater,
                callData: blockUpdaterInterface.encodeFunctionData('pendingReward', [
                  toSmallUnits(String(collateralBalance), tokenInfo.decimals),
                  holderInfos[0],
                  holderInfos[1],
                  holderInfos[2],
                ]),
                functionSignature: 'pendingReward',
              })
            }
          } else {
            pendingRewardCalls.push(
              ...holderInfoMapping.map((holder, index) => {
                return {
                  target: tokenInfo.blockUpdater,
                  callData: blockUpdaterInterface.encodeFunctionData('pendingReward', [
                    toSmallUnits(String(index === 0 ? walletBalance : collateralBalance), tokenInfo.decimals),
                    holder[0],
                    holder[1],
                    holder[2],
                  ]),
                  functionSignature: 'pendingReward',
                }
              }),
            )
          }
          const {
            returnData: { pendingReward },
          } = await multicall.aggregate(pendingRewardCalls)

          if (pendingRewardCalls.length === 1) {
            const reward = pendingReward[0]
            if (walletHasRweards) {
              tokenRewards.walletRewards = {
                epochReward: reward.epochReward,
              }
              tokenRewards.walletEstimateDailyReward = getEstimatedDailyReward(
                positionInfo.lastEpochReward,
                toSmallUnits(String(walletBalance), tokenInfo.decimals),
              )
            }
            if (positionHasRweards) {
              tokenRewards.avatarRewards = {
                epochReward: reward.epochReward,
              }
              tokenRewards.avatarEstimateDailyReward = getEstimatedDailyReward(
                positionInfo.lastEpochReward,
                toSmallUnits(String(collateralBalance), tokenInfo.decimals),
              )
            }
          } else {
            pendingReward.forEach((reward, index) => {
              if (index === 0) {
                tokenRewards.walletRewards = {
                  epochReward: reward.epochReward,
                }
                tokenRewards.walletEstimateDailyReward = getEstimatedDailyReward(
                  positionInfo.lastEpochReward,
                  toSmallUnits(String(walletBalance), tokenInfo.decimals),
                )
              }
              if (index === 1) {
                tokenRewards.avatarRewards = {
                  epochReward: reward.epochReward,
                }
                tokenRewards.avatarEstimateDailyReward = getEstimatedDailyReward(
                  positionInfo.lastEpochReward,
                  toSmallUnits(String(collateralBalance), tokenInfo.decimals),
                )
              }
            })
          }
          setRewardsData((state) => {
            return {
              ...state,
              [key]: tokenRewards,
            }
          })
        }
      }
    }
  }, [account, allowedTokens, chainId, isLogin, positionAll, walletTokenBalance])

  useEffect(() => {
    getPendingReward()
  }, [getPendingReward])

  useEffect(() => {
    let _walletTotal = 0
    let _avatarTotal = 0
    let _dailyInterestRewards = 0
    let _dailyObligation = 0
    const _rewardsAll = []

    for (const item of Object.values(positionAll)) {
      const { mintTokenInfo, positionAddress, isEnable } = item

      const prices = tokenPrice[positionAddress]
      if (!prices || !Object.prototype.hasOwnProperty.call(prices, mintTokenInfo.symbol)) {
        continue
      }
      let avatarAssetList = Object.keys(valueToKeyMap).reduce((acc, key) => {
        return {
          ...acc,
          [key]: 0,
        }
      }, {})

      if (isEnable && allowedTokens[positionAddress]) {
        const { balances, tokens } = allowedTokens[positionAddress]
        avatarAssetList = Object.keys(balances).reduce((acc, key) => {
          return {
            ...acc,
            [key]: balances[key].isZero() ? 0 : (toLargeUnits(balances[key], tokens[key].decimals, true) as number),
          }
        }, {})
      }

      // settlementCurrency transfrom to USDT
      const settlementCurrencyToUSD = (prices?.USDT && 1 / prices?.USDT?.price.toNumber()) * 10 ** 8 || 0
      const currentWBTCPrice = Number(
        toLargeUnits(prices[mintTokenInfo.symbol].price, mintTokenInfo.settlementCurrency.decimals),
      )
      _walletTotal += walletTokenBalance[mintTokenInfo.symbol] * currentWBTCPrice * settlementCurrencyToUSD
      _avatarTotal += avatarAssetList[mintTokenInfo.symbol] * currentWBTCPrice * settlementCurrencyToUSD
      if (rewardsData[mintTokenInfo.symbol]) {
        const walletEstimateDailyReward =
          rewardsData[mintTokenInfo.symbol].walletEstimateDailyReward / 10 ** mintTokenInfo.settlementCurrency.decimals // TODO resolve overflow
        const avatarEstimateDailyReward =
          rewardsData[mintTokenInfo.symbol].avatarEstimateDailyReward / 10 ** mintTokenInfo.settlementCurrency.decimals // TODO resolve overflow
        _dailyInterestRewards += (walletEstimateDailyReward + avatarEstimateDailyReward) * settlementCurrencyToUSD
        _dailyObligation +=
          (getEstimatedDailyReward(item.lastEpochReward, item.balance) /
            10 ** mintTokenInfo.settlementCurrency.decimals) *
          settlementCurrencyToUSD

        const walletRewards = Number(
          toLargeUnits(
            rewardsData[mintTokenInfo.symbol].walletRewards.epochReward,
            mintTokenInfo.settlementCurrency.decimals,
          ),
        )
        const avatarRewards = Number(
          toLargeUnits(
            rewardsData[mintTokenInfo.symbol].avatarRewards.epochReward,
            mintTokenInfo.settlementCurrency.decimals,
          ),
        )
        const lpAllRewards = walletRewards + avatarRewards
        const rewardsTransfromToUsdt = (walletRewards + avatarRewards) * settlementCurrencyToUSD
        _rewardsAll.push({
          rewards: lpAllRewards,
          usdt: rewardsTransfromToUsdt,
          lptoken: mintTokenInfo.symbol,
        })
      }
      setWalletTotal(_walletTotal)
      setAvatarTotal(_avatarTotal)
      setDailyInterestRewards(_dailyInterestRewards)
      setDailyObligation(_dailyObligation)
      setAllRewards(_rewardsAll)
    }
  }, [allowedTokens, positionAll, rewardsData, tokenPrice, walletTokenBalance])

  const totalRwards = useMemo(() => {
    const rewardsList: any = {}
    for (const token of allRewards) {
      const { lptoken, rewards, usdt } = token
      const settlementSymbol =
        mintTokenChainConfigs[chainId as SupportedChainId].tokens[valueToKeyMap[lptoken] as MintTokenKey]
          .settlementCurrency.symbol
      if (rewardsList[settlementSymbol]) {
        rewardsList[settlementSymbol].rewards += rewards
        rewardsList[settlementSymbol].usdt += usdt
      } else {
        rewardsList[settlementSymbol] = {
          rewards,
          usdt
        }
      }
    }
    return rewardsList
  }, [allRewards, chainId])
  const totalRwardsUsdt = useMemo(() => {
    const totalUSDT = allRewards.reduce((acc, reward) => acc + reward.usdt, 0)
    return TokenUtils.formatCurrencyTwoDecimals(totalUSDT)
  }, [allRewards])

  const rewardPannel = useMemo(() => {
    return Object.keys(totalRwards).map((key) => (
      <Grid container style={{ marginBottom: 18, lineHeight: '20px', fontSize: '14px' }}>
        <Grid item xs={6}>
          <span>{(totalRwards[key] && TokenUtils.formatDecimalString(totalRwards[key].rewards, 4)) || 0} {key}</span>
        </Grid>
        <Grid item xs={6} dir="rtl">
          <span>${TokenUtils.formatCurrencyTwoDecimals(totalRwards[key].usdt)}≈</span>
        </Grid>
      </Grid>
    ))
  }, [totalRwards])
  return (
    <Wrapper>
      <BTitle>My Page</BTitle>
      <Box>
        <Grid container spacing={2}>
          <Grid item xs={8}>
            <Grid container spacing={2}>
              <Grid item xs={12}>
                <CommonCard>
                  <SubTitle>Holdings - Synthetic Hashpower Token</SubTitle>
                  <Grid container>
                    <Grid item xs={6}>
                      <h4>Hashpower Token in Wallet </h4>
                      <p>
                        <sub>≈</sub>${TokenUtils.formatNumberWithUnits(walletTotal)}
                      </p>
                    </Grid>
                    <Grid item xs={6}>
                      <h4>Hashpower Token in Collateral</h4>
                      <p>
                        <sub>≈</sub>${TokenUtils.formatNumberWithUnits(avatarTotal)}
                      </p>
                    </Grid>
                  </Grid>
                </CommonCard>
              </Grid>
              <Grid item xs={12} style={{ paddingTop: 20 }}>
                <CommonCard style={{ paddingBottom: 33 }}>
                  <SubTitle>Claimable Rewards</SubTitle>
                  <Grid container spacing={2}>
                    <Grid item xs={6}>
                      {rewardPannel}
                      <Divider style={{ marginBottom: 18 }} />
                      <Grid container style={{ marginBottom: 20, lineHeight: '20px', fontSize: '14px' }}>
                        <Grid item xs={6}>
                          <span>Total</span>
                        </Grid>
                        <Grid item xs={6} dir="rtl">
                          <span>${totalRwardsUsdt}≈</span>
                        </Grid>
                      </Grid>
                    </Grid>
                    <Grid item xs={6} style={{ paddingTop: 0, paddingLeft: 97 }}>
                      <img
                        src="/images/eth.png"
                        style={{ width: 108, height: 108, margin: '5px 0 0 35px', display: 'block' }}
                        alt="illustration"
                      />
                      <ClaimButton
                        disabled={claimStatus}
                        style={{ margin: '30px 0 0' }}
                        className={`${claimStatus ? 'disabled' : ''}`}
                        onClick={claimAll}
                      >
                        Claim All Rewards
                      </ClaimButton>
                    </Grid>
                  </Grid>
                </CommonCard>
              </Grid>
            </Grid>
          </Grid>
          <Grid item xs={4}>
            <CommonCard className="rightPanel">
              <SubTitle>Epoch {prevEpoch.epoch} Balance</SubTitle>
              <Grid container>
                <Grid item xs={12} style={{ marginBottom: 30 }}>
                  <h4>Rewards Earned</h4>
                  <p>
                    <sub>≈</sub>${formatNumberWithUnits(dailyInterestRewards)}
                  </p>
                </Grid>
                <Grid item xs={12} style={{ marginBottom: 30 }}>
                  <h4>Interest Paid</h4>
                  <p>
                    <sub>≈</sub>${formatNumberWithUnits(dailyObligation)}
                  </p>
                </Grid>
                <Grid item xs={12} style={{ marginBottom: 30 }}>
                  <Divider />
                </Grid>

                <Grid item xs={12} style={{ marginBottom: 36 }}>
                  <h4>Net Rewards</h4>
                  <p>
                    <sub>≈</sub>
                    {formatNumberWithUnits(dailyInterestRewards - dailyObligation).includes('-')
                      ? formatNumberWithUnits(dailyInterestRewards - dailyObligation).replace('-', '-$')
                      : `$${formatNumberWithUnits(dailyInterestRewards - dailyObligation)}`}
                  </p>
                </Grid>
              </Grid>
            </CommonCard>
          </Grid>
        </Grid>
        <div style={{ width: '100%' }}>
          <Tabs
            tabsStyle={{
              minHeight: 36,
              paddingLeft: 21,
            }}
            tabStyle={{
              paddingTop: 0,
              paddingBottom: 0,
              minHeight: 32,
              lineHeight: '32px',
            }}
            tab={[
              {
                label: 'All',
                component: (
                  <>
                    <HoldingsWallet rewardsData={rewardsData} />
                    {/* <HoldingsAvatar rewardsData={rewardsData} />    / */}
                    <OutstandingDebt />
                  </>
                ),
              },
              {
                label: 'Holdings',
                component: <HoldingsWallet rewardsData={rewardsData} />,
              },
              // {
              //   label: 'Holdings - Avatar',
              //   component: <HoldingsAvatar rewardsData={rewardsData} />
              // },
              {
                label: 'Outstanding Debt',
                component: <OutstandingDebt />,
              },
            ]}
          />
        </div>
      </Box>
    </Wrapper>
  )
}

export default React.memo(MyPage)
