import React, { useMemo } from 'react'
import { Link } from 'react-router-dom'
import Card from 'components/Card'
// import { BigNumber } from '@ethersproject/bignumber'
import Table, { StyledTableCell } from 'components/Table'
import { Stack } from '@mui/material'
import { useShortActiveAction, useShortPendingAction } from 'state/Short/hooks'
import { TokenUtils, toLargeUnits } from 'utils/transformHelper'
import { usePositionAction, usePositionInfo } from 'hooks/news/usePoistion'
import { SupportedTokens, valueToKeyMap } from 'config/constants/tokenConfig'
import { computedPI, computedTC } from 'utils/computed'
import { TokenType } from 'state/Short/shortSlice'
import Title from './Title'
import HistoryIcon from './icon/History'

const columns = [
  {
    id: 'symbol',
    numeric: false,
    disablePadding: true,
    isSort: true,
    label: 'Ticker',
  },
  {
    id: 'markPrice',
    numeric: true,
    disablePadding: true,
    isSort: true,
    label: 'Mark Price',
  },
  {
    id: 'totalAssetsValue',
    numeric: true,
    disablePadding: false,
    isSort: true,
    label: 'Collateral Value',
  },
  {
    id: 'collateralRatio',
    numeric: true,
    disablePadding: false,
    isSort: true,
    label: 'Collateral Ratio',
  },
  {
    id: 'prepaidInterestCoverage',
    numeric: true,
    disablePadding: false,
    isSort: true,
    label: 'Prepaid Interest Coverage',
  },
  {
    id: 'outstandingDebt',
    numeric: true,
    disablePadding: false,
    isSort: true,
    label: 'Outstanding Debt Position',
  },
  // {
  //   id: 'dailyObligation',
  //   numeric: true,
  //   disablePadding: false,
  //   isSort: true,
  //   label: 'Daily  Obligation',
  // },
  {
    id: 'action',
    numeric: false,
    disablePadding: false,
    isSort: false,
    label: 'Action',
  },
]

function OutstandingDebt() {
  const { setActiveTab, setActionInfo, setActiveAmount, clearActive } = useShortActiveAction()

  const { clearAllPendingAction } = useShortPendingAction()
  const { positionAll, tokenPrice, allowedTokens, currentMintToken } = usePositionInfo()
  const { handleChangeCurrentPosition } = usePositionAction()



  const dataSource = useMemo(() => {
    const list = Object.values(positionAll)

    return list.map((item) => {
      let markPrice = 0
      const { settlementCurrency } = item.mintTokenInfo
      let _avatarAssetList = Object.keys(valueToKeyMap).reduce((acc, key) => {
        return {
          ...acc,
          [key]: 0,
        }
      }, {}) as Record<SupportedTokens, number>
      if (item.isEnable && tokenPrice[item.positionAddress]) {
        markPrice = toLargeUnits(
          tokenPrice[item.positionAddress][item.mintTokenInfo.symbol]?.price,
          settlementCurrency.decimals,
          true
        ) as number
      }
      if (item.isEnable && allowedTokens[item.positionAddress]) {
        const { balances, tokens } = allowedTokens[item.positionAddress]
        if (balances && tokens) {
          _avatarAssetList = Object.keys(balances).reduce((acc, key) => {
            return {
              ...acc,
              [key]:tokens[key] ? toLargeUnits(balances[key], tokens[key].decimals, true) as number : 0,
            }
          }, {
            ..._avatarAssetList
          })
        }

      }
      const { prepaidInterestCoverage, collateralRatio } = item
      const { interestGuaranteed, obligation, epochDebt, estimatedObligationToday } = item?.positionStatus
      const prepaidInterest = computedPI({
        interestGuaranteed: Number(interestGuaranteed),
        obligation: Number(obligation),
        epochDebt: Number(epochDebt),
        estimatedObligationToday: Number(estimatedObligationToday),
      })
      const totalAssetsValue = toLargeUnits(
        computedTC({
          collateralValue: Number(item.positionStatus.collateralValue),
          prepaidInterest,
        }),
        item?.mintTokenInfo.settlementCurrency.decimals,
      ) as number
      return {
        symbol: item.mintTokenInfo.symbol,
        logo: item.mintTokenInfo.logo,
        markPrice,
        displayMarkPrice: `${TokenUtils.formatFourDecimals(markPrice)} ${settlementCurrency.symbol}`,
        avatarBalance: Number(_avatarAssetList[item.mintTokenInfo.symbol]),
        prepaidInterestCoverage,
        collateralRatio,
        totalAssetsValue,
        displayTotalAssetsValue: `${TokenUtils.formatFourDecimals(totalAssetsValue)} ${item?.mintTokenInfo.settlementCurrency.symbol}`,
        displayCoverage:
          prepaidInterestCoverage === Number.POSITIVE_INFINITY ? '+∞' : `${Math.floor(prepaidInterestCoverage)} days`,
        displayRatio:
          collateralRatio === Number.POSITIVE_INFINITY
            ? '+∞'
            : (collateralRatio && `${TokenUtils.formatTokenAmount(collateralRatio, 2)}%`) || `${collateralRatio}`,
        outstandingDebt: TokenUtils.formatFourDecimals(toLargeUnits(item.balance, item.mintTokenInfo.decimals, true) as number),
      }
    })
  }, [allowedTokens, positionAll, tokenPrice])


  return (
    <Card style={{ marginBottom: 20 }}>
      <Title>
        <h3>Outstanding Debt</h3>
        <Link to="/history/obligation">
          <HistoryIcon />
          Interest Paid History
        </Link>
      </Title>
      <Table
        columns={columns}
        dataSource={dataSource}
        pagination={false}
        isMyPage
        content={(row, index, labelId) => (
          <>
            <StyledTableCell style={{ paddingLeft: 0 }} component="th" id={labelId} scope="row">
              <img
                src={row.logo}
                style={{ display: 'block', marginRight: 7, float: 'left', width: 20, height: 20 }}
                alt="icon"
              />
              {row.symbol}
            </StyledTableCell>
            <StyledTableCell>{row.displayMarkPrice}</StyledTableCell>
            <StyledTableCell>
              {row.displayTotalAssetsValue}
            </StyledTableCell>
            <StyledTableCell>
              {row.displayRatio}
            </StyledTableCell>
            <StyledTableCell>
              {row.displayCoverage}
            </StyledTableCell>
            <StyledTableCell>
              {row.outstandingDebt}
            </StyledTableCell>
            {/* <StyledTableCell>{ Number(row.position) && row.positionInfoValues ? `≈ ${formatWBTC(row.positionInfoValues.totalObligation)}` : 0 } WBTC</StyledTableCell> */}
            <StyledTableCell>
              <Stack direction="row" spacing={2}>
                <Link
                  style={{ color: '#2AA1FF', fontSize: '14px', fontFamily: 'PingFangSC-Regular,sans-serif' }}
                  to="/collateral/manage"
                  onClick={() => {
                    // changeCPT(row)
                    handleChangeCurrentPosition(row.symbol)
                  }}
                >
                  Manage Collateral
                </Link>
                <Link
                  style={{ color: '#2AA1FF', fontSize: '14px', fontFamily: 'PingFangSC-Regular,sans-serif' }}
                  to="/shortneutralize"
                  onClick={() => {
                    clearAllPendingAction()
                    setActiveAmount('')
                    clearActive()
                    setActiveTab(2)
                    setActionInfo({ token: currentMintToken as unknown as string, tokenType: TokenType.NEUTRALIZE })
                    // changeCPT(row)
                    handleChangeCurrentPosition(row.symbol)
                  }}
                >
                  Neutralize
                </Link>
              </Stack>
            </StyledTableCell>
          </>
        )}
      />
    </Card>
  )
}

export default OutstandingDebt
