import React, { useMemo } from 'react'
import { Link } from 'react-router-dom'
import Card from 'components/Card'
import { useWeb3React } from 'web3-react-core'
import Table, { StyledTableCell } from 'components/Table'
import { TokenUtils, toLargeUnits } from 'utils/transformHelper'
import { usePositionInfo } from 'hooks/news/usePoistion'
import useHashPowerToken from 'hooks/useHashPowerToken'
import { SupportedTokens, valueToKeyMap } from 'config/constants/tokenConfig'
import Action from './Action'
import Title from './Title'
import HistoryIcon from './icon/History'
import { RewardData } from '../type'

const columns = [
  {
    id: 'symbol',
    numeric: false,
    disablePadding: true,
    isSort: true,
    label: 'Ticker',
  },
  {
    id: 'markPrice',
    numeric: true,
    disablePadding: true,
    isSort: true,
    label: 'Mark Price',
  },
  {
    id: 'walletBalance',
    numeric: true,
    disablePadding: false,
    isSort: true,
    label: 'In Wallet',
  },
  {
    id: 'avatarBalance',
    numeric: true,
    disablePadding: false,
    isSort: true,
    label: 'In Collateral',
  },
  {
    id: 'claimableWallet',
    numeric: true,
    disablePadding: false,
    isSort: true,
    label: 'Claimable - In Wallet',
  },
  {
    id: 'claimableAvatar',
    numeric: true,
    disablePadding: false,
    isSort: true,
    label: 'Claimable - In Collateral',
  },
  {
    id: 'action',
    numeric: false,
    disablePadding: false,
    isSort: false,
    label: 'Action',
  },
]
/*
 * will
 * add filter
 *
 *
 * */
function HoldingsWallet({ rewardsData }: {
  rewardsData: RewardData
}) {
  const { walletTokenBalance } = useHashPowerToken()
  const { account } = useWeb3React()
  const { positionAll, tokenPrice, allowedTokens } = usePositionInfo()

  const dataSource = useMemo(() => {
    const list = Object.values(positionAll)

    return list.map((item) => {
      let markPrice = 0
      const { settlementCurrency } = item.mintTokenInfo
      let _avatarAssetList = Object.keys(valueToKeyMap).reduce((acc, key) => {
        return {
          ...acc,
          [key]: 0,
        }
      }, {}) as Record<SupportedTokens, number>
      if (item.isEnable && tokenPrice[item.positionAddress]) {
        markPrice = toLargeUnits(
          tokenPrice[item.positionAddress][item.mintTokenInfo.symbol]?.price,
          settlementCurrency.decimals,
          true
        ) as number
      }
      if (item.isEnable && allowedTokens[item.positionAddress]) {
        const { balances, tokens } = allowedTokens[item.positionAddress]
        if (balances && tokens) {
          _avatarAssetList = Object.keys(balances).reduce((acc, key) => {
            return {
              ...acc,
              [key]:tokens[key] ?  toLargeUnits(balances[key], tokens[key].decimals, true) as number: 0,
            }
          }, {
            ..._avatarAssetList
          })
        }

      }
      const claimableWallet = toLargeUnits(rewardsData[item.mintTokenInfo.symbol].walletRewards.epochReward, settlementCurrency.decimals, true) as number;
      const claimableAvatar = toLargeUnits(rewardsData[item.mintTokenInfo.symbol].avatarRewards.epochReward, settlementCurrency.decimals, true) as number;
      return {
        symbol: item.mintTokenInfo.symbol,
        logo: item.mintTokenInfo.logo,
        markPrice,
        displayMarkPrice: `${TokenUtils.formatFourDecimals(markPrice)} ${settlementCurrency.symbol}`,
        walletBalance: Number(TokenUtils.formatFourDecimals(walletTokenBalance[item.mintTokenInfo.symbol])),
        avatarBalance: Number(_avatarAssetList[item.mintTokenInfo.symbol]),
        claimableWallet,
        claimableAvatar,
        displayClaimableWallet: `${TokenUtils.formatFourDecimals(claimableWallet)} ${settlementCurrency.symbol}`,
        displayClaimableAvatar: `${TokenUtils.formatFourDecimals(claimableAvatar)} ${settlementCurrency.symbol}`,
        mintTokenInfo: item.mintTokenInfo,
        isEnable: item.isEnable,
        positionAddress: item.positionAddress,
      }
    })
  }, [allowedTokens, positionAll, rewardsData, tokenPrice, walletTokenBalance])

  return (
    <Card style={{ marginBottom: 20 }}>
      <Title>
        <h3>Holdings - Hashpower Token</h3>
        <Link to="/history/claimed/0">
          <HistoryIcon /> Claimed History
        </Link>
      </Title>
      <Table
        columns={columns}
        dataSource={dataSource}
        pagination={false}
        isMyPage
        content={(row, index, labelId) => (
          <>
            <StyledTableCell style={{ paddingLeft: 0 }} component="th" id={labelId} scope="row">
              <img
                src={row.logo}
                style={{ display: 'block', marginRight: 7, float: 'left', width: 20, height: 20 }}
                alt="icon"
              />
              {row.symbol}
            </StyledTableCell>
            <StyledTableCell>{row.displayMarkPrice}</StyledTableCell>
            <StyledTableCell>{row.walletBalance}</StyledTableCell>
            <StyledTableCell>
              {row.avatarBalance}
            </StyledTableCell>
            <StyledTableCell>
              {row.displayClaimableWallet}
            </StyledTableCell>
            <StyledTableCell>
              {row.displayClaimableAvatar}
            </StyledTableCell>
            <StyledTableCell>
              <Action row={row} address={account} rewardsData={rewardsData[row.symbol]} />
            </StyledTableCell>
          </>
        )}
      />
    </Card>
  )
}
export default HoldingsWallet
